import React from "react";
import { styles } from "../styles";
import { Icon } from "@iconify/react";
import { motion } from "framer-motion";

const Navbar = () => {
  return (
    <div className="flex flex-row w-full justify-between items-center px-6 py-2 md:px-20 md:py-5 absolute">
      <a href="#home" className="font-regular font-bold text-[30px] cursor-pointer tracking-tight">
        Facundo
      </a>
      <div className="md:flex md:flex-row justify-between items-center hidden">
        <a href="#personal" className={styles.navbtn}>Personal</a>
        <a href="#habilidades" className={styles.navbtn}>Habilidades</a>
        <a href="#proyectos" className={styles.navbtn}>Proyectos</a>
        <a href="#contacto"
          className={`${styles.navbtn} rounded-[20px] border-2 border-black py-1 px-6 flex flex-row justify-between items-center hover:bg-black hover:text-white transition-all	`}
        >
          Contacto
          <Icon
            icon="material-symbols:arrow-forward-rounded"
            className="ml-2"
          />
        </a>
      </div>
      <button className="flex md:hidden">
        <Icon icon="material-symbols:menu-rounded" height={30} />
      </button>
    </div>
  );
};

export default Navbar;
